import { createServerClient } from "@supabase/ssr";
import { cookies } from "next/headers";
import BudgetClient from "./BudgetClient";

async function getBudgetsSSR(userId) {
  const cookieStore = cookies();

  const supabase = createServerClient(
    process.env.NEXT_PUBLIC_SUPABASE_URL,
    process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY,
    {
      cookies: {
        getAll() {
          return cookieStore.getAll();
        },
        setAll() {
          // Read-only for SSR
        },
      },
    }
  );

  const { data, error } = await supabase
    .from("budgets")
    .select("*")
    .eq("user_id", userId)
    .order("created_at", { ascending: false });

  return { data, error };
}

export default async function BudgetPage() {
  const cookieStore = cookies();

  const supabase = createServerClient(
    process.env.NEXT_PUBLIC_SUPABASE_URL,
    process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY,
    {
      cookies: {
        getAll() {
          return cookieStore.getAll();
        },
        setAll() {
          // Read-only for SSR
        },
      },
    }
  );

  // Get user session
  const {
    data: { session },
  } = await supabase.auth.getSession();

  if (!session) {
    // This will be handled by middleware, but as fallback
    return <div>Redirecting...</div>;
  }

  // Fetch budgets on the server
  const initialBudgets = await getBudgetsSSR(session.user.id);

  return <BudgetClient initialBudgets={initialBudgets} user={session.user} />;
}
